.group-select-container{
    z-index: 1;
    background-color: #16242e;
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: 2;
}

#group-prompt{
    margin-top: 75px;
}

.group-select{
    position:fixed;
    transform: translate(-50%,-50%);
    top:50%;
    left:50%;
    border-radius: 5px;
}

.group-select-off{
    display: none;
}

area:hover{
    cursor: pointer;
}