

.Cell{
    display: inline-block;
    word-wrap: break-word;
    width: 10%;
    height: 10%;
    background-color: rgb(211, 211, 181);
    border: 1px solid black;
    color:black;
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
    transition: all 2s;
    font-size: 20px;
    overflow: hidden;
}

.Cell-current{
    display: inline-block;
    word-wrap: break-word;
    width: 10%;
    height: 10%;
    background-color: indigo;
    border: 1px solid black;
    color:gold;
    border-radius: 5px;
    box-sizing: border-box;
    text-align: center;
    font-size: 20px;
    overflow: hidden;
}