.Welcome{
    color: white;
    text-align: center;
    height: 100vh;
    width: 100vw;
    font-size: calc(10px + 2vmin);
    position: relative;
    background-image: linear-gradient(to right, rgba(112, 128, 144, 0.95),rgba(128, 128, 128, 0.8)),url('./PunishmentHall.JPG');
    background-size: cover;
    background-position: top;
}

#Logo{
    position:fixed;
    top:50%;
    left:50%;
    transform: translate(-50%, -50%);
    font-size: 200px;
    background-image: url('./peach.gif');
    background-size:cover;
    background-position: left;
    background-repeat: no-repeat;
    height: 20vh;
    width: 20vw;
}

.English-select{
    background-image: linear-gradient(to right, rgba(112, 128, 144, 0.95),rgba(128, 128, 128, 0.8)), url('./PunishmentHall.JPG');
    background-size: cover;
    background-position: top;
    clip-path: polygon(0 0,30% 0, 30% 100%,0 100%);
    position: absolute;
    height: 100%;
    width:100%;
    z-index: 2;
    transition: all 1.5s;
}

.English-select::after{
    content: 'Start';
    position:fixed;
    font-size: 40px;
    left:100px;
    bottom:100px;
    z-index: 3;
    color: whitesmoke;
}

.English-select:hover{
    cursor: pointer;
    background-image: linear-gradient(to top, #0d324df5,#7f5a83f5), url('./Ludo.jpg');
    clip-path: polygon(0 0,70% 0, 70% 100%,0 100%);
    background-size: cover;
    background-position: left;
    z-index: 3;
}

.English-select:hover::before{
    content: "";
    font-size: 100px;
    color: white;
    position: absolute;
    left: 100px;
    top: 100px;
}

.English-select:hover::after{
    content: "English";
    font-size: 100px;
    color: white;
    position: absolute;
    left: 100px;
    bottom: 100px;
}

.Chinese-select{
    background-image: linear-gradient(to right, rgba(112, 128, 144, 0.95),rgba(128, 128, 128, 0.8)), url('./PunishmentHall.JPG');
    background-size: cover;
    background-position: top;
    position: absolute;
    clip-path: polygon(70% 0,100% 0, 100% 100%, 70% 100%);
    height: 100%;
    width:100%;
    transition: all 1.5s;
    z-index: 2;
}

.Chinese-select::after{
    content: '开始';
    position:fixed;
    font-size: 40px;
    right:100px;
    bottom:100px;
    z-index: 3;
    color: whitesmoke;
}

.Chinese-select:hover{
    cursor: pointer;
    background-image: linear-gradient(to top, #0d324df5,#7f5a83f5), url('./Ludo.jpg');
    background-size: cover;
    clip-path: polygon(30% 0,100% 0, 100% 100%, 30% 100%);
    background-position: right;
    z-index: 3;
}

.Chinese-select:hover::before{
    content: "";
    font-size: 100px;
    color: white;
    position: absolute;
    right: 100px;
    top: 100px;
}

.Chinese-select:hover::after{
    content: "简体中文";
    font-size: 100px;
    color: white;
    position: absolute;
    right: 100px;
    bottom: 100px;
}

#English-start{
    position: absolute;
    bottom: 100px;
    left: 100px;
    color: whitesmoke;
    z-index: 3;
}
#Chinese-start{
    position: absolute;
    bottom: 100px;
    right: 100px;
    color: whitesmoke;
    z-index: 3;
}
